  /* object-fit: contain; */
  width: auto;
  height: auto;
  max-width: 100%;
  max-height: 100%;
}
body {
  overflow: hidden;
}

html,
body,
#root {
  margin: 0px;
  height: 100%;
  width: 100%;
}

.App-header {
  background-color: white;
  height: 100%;
  width: 100%;

  /* display: grid; */
  /* grid-template-rows: [header-start] max-content [image-start] auto [image-end];
  grid-template-columns: [image-start] auto [image-end]; */
  /* overflow: hidden; */
  display: flex;
  flex-direction: column;
  /* align-items: center;
  justify-content: flex-start; */
  font-size: calc(10px + 2vmin);
  color: gray;
}

.header {
  flex: 0 0 auto;
  min-height: 0;
  padding-top: 1rem;
}

.footer {
  flex: 0 0 auto;
  min-height: 0;
}

.MuiCard-root {
  /* overflow: auto !important; */
}

.footer .MuiCardHeader-root {
  padding-bottom: 0;
  text-align: left;
}

.footer .MuiCardContent-root {
  padding-top: 0;
  text-align: left;
  overflow: auto;
  height: 5rem;
}

h5.desc-header {
  text-transform: uppercase;
  opacity: 0.5;
  margin: 0;
  font-size: 1.4rem;
}

p.desc-body {
  margin: 0;
  font-size: 1.2rem;
  font-family: Arial;
}

.content {
  flex: 1 1 auto;
  min-height: 0;
  overflow: auto;
  width: 100%;
  align-self: stretch;
  display: flex;
  flex-direction: column;
  justify-content: center;
}

.map-button {
  margin: 0.5rem !important;
}

.image-url {
  width: 50rem;
  max-width: calc(100% - 2rem);
  margin-top: 1rem !important;
}

